import React, { useLayoutEffect, useState } from "react";
import styled from "styled-components";
import { Link } from "../components/Link";
import { VisuallyHidden } from "./VisuallyHidden";
import { MdMenu } from "react-icons/md";

export const MenuLink = styled(Link)`
  color: inherit;
  text-decoration: none;

  display: block;
  border-bottom: none;

  &:hover {
    text-decoration: underline;
  }
`;

const MenuWrapper = styled.div`
  background: ${(p) => p.theme.colors.text};
  color: ${(p) => p.theme.colors.textInverse};
  position: fixed;
  height: 100%;
  width: 100%;
  transition: transform 0.3s;
  transform: translateX(${(p) => (p.isVisible ? 0 : "-100%")});
  top: ${(p) => p.theme.sizes.menuMobile};

  ${MenuLink} {
    visibility: ${(p) => (p.isVisible ? "visible" : "hidden")};
  }

  ${(p) => p.theme.mq.tablet} {
    width: 20rem;
  }

  ${(p) => p.theme.mq.desktop} {
    top: 0;
    transform: translateX(0);

    ${MenuLink} {
      visibility: visible;
    }
  }
`;

const MenuButton = styled.button`
  background: transparent;
  border: none;
  color: ${(p) => p.theme.colors.textInverse};
  font-size: ${(p) => p.theme.fontSizes[6]};
  padding: 0 ${(p) => p.theme.spaces[4]};
  height: ${(p) => p.theme.sizes.menuMobile};

  ${(p) => p.theme.mq.desktop} {
    display: none;
  }
`;

const Nav = styled.nav`
  background: ${(p) => p.theme.colors.text};
  position: fixed;
  width: 100%;
  z-index: 1;

  ${(p) => p.theme.mq.desktop} {
    top: 0;
    padding: revert;
    position: revert;
    top: revert;
    width: revert;
  }
`;

export const Menu = ({ children }) => {
  const [isVisible, setIsVisible] = useState(false);

  /**
   * Prevent body from scrolling when the menu is opened
   */
  useLayoutEffect(() => {
    if (isVisible) {
      const prev = document.body.style.overflow;
      document.body.style.overflow = "hidden";

      return () => {
        document.body.style.overflow = prev;
      };
    }
  }, [isVisible]);

  return (
    <Nav id="left-menu">
      <MenuButton
        onClick={() => setIsVisible((s) => !s)}
        aria-controls="left-menu"
        aria-expanded={isVisible}
      >
        <VisuallyHidden>Ouvrir le menu</VisuallyHidden>
        <MdMenu />
      </MenuButton>

      <MenuWrapper isVisible={isVisible}>{children}</MenuWrapper>
    </Nav>
  );
};
