import { useEffect, useState } from "react";
import { modules } from "../../modules";

const getModule = () => {
  return modules["posts"];
};

export const useCurrentModule = () => {
  const [module, setModule] = useState();

  useEffect(() => {
    const actualModule = getModule(window.location.pathname);
    setModule(actualModule);
  }, []);

  return module;
};
