import React from "react";
import { MDXProvider } from "@mdx-js/react";
import Helmet from "react-helmet";
import { Container } from "../components/Container";
import { ThemeProvider } from "../components/ThemeProvider";
import { ModuleMenu } from "../modules/module/ModuleMenu";
import { ModulePageLayout } from "../modules/module/ModulePageLayout";
import { GlobalStyles } from "../components/GlobalStyles";
import { Link } from "../components/Link";
import { Text } from "../components/Text";
import { Code } from "../components/Code";
import { Main } from "../components/Main";
import { SkipToContent } from "../components/SkipToContent";
import { List, ListItem } from "../components/List";
import { Em } from "../components/Em";
import { Strong } from "../components/Strong";
import { Img } from "../components/Img";
import { Iframe } from "../components/Iframe";
import { Content } from "../components/Content";
import { Aside } from "../components/Aside";

// component replacements
const shortcodes = {
  a: Link,
  h1: () => {
    throw new Error("H1 is already used in frontmatter. Can't recreate one");
  },
  h2: (props) => <Text {...props} as="h2" />,
  h3: (props) => <Text {...props} as="h3" />,
  h4: (props) => <Text {...props} as="h4" />,
  h5: (props) => <Text {...props} as="h5" />,
  h6: (props) => <Text {...props} as="h6" />,
  span: (props) => <Text {...props} as="span" />,
  p: (props) => <Text {...props} as="p" />,
  pre: Code,
  ul: List,
  li: ListItem,
  em: Em,
  strong: Strong,
  img: Img,
  iframe: Iframe,
  aside: Aside,
};

const SiteRoot = `https://mfrachet.github.io`;
const CourseTemplate = ({ children, pageContext, location , ...a}) => {
  const { frontmatter } = pageContext;

  return (
    <ThemeProvider>
      <Helmet>
        <html lang="en" />
        <title>{frontmatter.title}</title>
        <meta name="description" content={frontmatter.excerpt} />
        <meta name="twitter:creator" content="@mfrachet" />
        <meta name="twitter:card" content="summary" />
        <meta property="og:url" content={`${SiteRoot}${location.pathname}`} />
        <meta property="og:type" content="article" />
        <meta property="og:title" content={frontmatter.title} />
        <meta property="og:description" content={frontmatter.excerpt} />
        {frontmatter?.metaImage?.publicURL && (
          <meta
            property="og:image"
            content={`${SiteRoot}${frontmatter?.metaImage?.publicURL}`}
          />
        )}

        {frontmatter.metaKeywords && (
          <meta name="keywords" content={frontmatter.metaKeywords} />
        )}
      </Helmet>

      <SkipToContent />

      <Container>
        <ModulePageLayout>
          <ModuleMenu />

          <Content>
            <Main>
              <Text as="h1">{frontmatter.title}</Text>
              <MDXProvider components={shortcodes}>{children}</MDXProvider>
            </Main>
          </Content>
        </ModulePageLayout>
      </Container>

      <GlobalStyles />
    </ThemeProvider>
  );
};

export default CourseTemplate;
