import React from "react";
import styled, { css } from "styled-components";
import { MdArrowBack } from "react-icons/md";
import { Menu, MenuLink } from "../../components/Menu";
import { Text } from "../../components/Text";
import { Box } from "../../components/Box";
import { useCurrentModule } from "./useCurrentModule";
import { VisuallyHidden } from "../../components/VisuallyHidden";
import { ModuleTimeline } from "./ModuleTimeline";

const ModuleMenuWrapper = styled.div`
  position: relative;
`;

export const ModuleMenu = () => {
  const module = useCurrentModule();

  // Generated on the server
  if (!module) {
    return null;
  }

  return (
    <ModuleMenuWrapper>
      <Menu>
        <Box padding={4}>
          <MenuLink
            to={"/"}
            css={css`
              display: inline-block;
            `}
          >
            <MdArrowBack />
            <VisuallyHidden>Back to the homepage</VisuallyHidden>
          </MenuLink>

          <Text as="h2">{module.title}</Text>
          <ModuleTimeline links={module.links} LinkAs={MenuLink} />
        </Box>
      </Menu>
    </ModuleMenuWrapper>
  );
};
